<template>
  <view class="receiver-admin-page">
    <view class="header">
      <text class="title">{{ fahuiType === 'joint' ? 'ÂêàÂùõÊ≥ï‰ºö' : '‰∏ìÂú∫Ê≥ï‰ºö' }}Êî∂‰ª∂‰∫∫‰ø°ÊÅØÁÆ°ÁêÜ</text>
    </view>
    <view class="switch-row">
      <text class="switch-label">ÂêØÁî®Êî∂‰ª∂‰ø°ÊÅØÊ®°Âùó</text>
      <switch :checked="enabled" @change="onSwitchChange" />
    </view>
    <view v-if="loading" class="loading">Âä†ËΩΩ‰∏≠...</view>
    <view v-else>
      <view v-if="receivers.length === 0" class="empty">ÊöÇÊó†Êî∂‰ª∂‰∫∫‰ø°ÊÅØ</view>
      <view v-else class="receiver-list">
        <view class="receiver-item" v-for="item in receivers" :key="item._id">
          <view class="info">
            <view class="info-row"><text class="label">Êî∂‰ª∂‰∫∫Ôºö</text><text class="value">{{ item.name }}</text></view>
            <view class="info-row"><text class="label">ÁîµËØùÔºö</text><text class="value">{{ item.phone }}</text></view>
            <view class="info-row"><text class="label">Âú∞ÂùÄÔºö</text><text class="value">{{ item.address }}</text></view>
            <view class="info-row" v-if="item.remark"><text class="label">Â§áÊ≥®Ôºö</text><text class="value">{{ item.remark }}</text></view>
          </view>
          <button class="delete-btn" @click="del(item._id)">Âà†Èô§</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

// ÂØºÂÖ•‰∫ëÂØπË±°
const fahuiManagement = uniCloud.importObject('fahui-management')

export default {
  components: { uniPopup },
  data() {
    return {
      fahuiType: 'special', // ÈªòËÆ§‰∏ìÂú∫Ê≥ï‰ºö
      receivers: [],
      loading: true,
      enabled: false,
    }
  },
  onLoad(options) {
    console.log('üîç È°µÈù¢Âä†ËΩΩÔºåÊé•Êî∂Âà∞ÁöÑÂèÇÊï∞:', options)
    
    // Ëé∑ÂèñÊ≥ï‰ºöÁ±ªÂûãÂèÇÊï∞
    if (options.type) {
      this.fahuiType = options.type;
    } else {
      // Â¶ÇÊûúÊ≤°Êúâ‰º†ÈÄítypeÂèÇÊï∞ÔºåÈªòËÆ§‰∏∫‰∏ìÂú∫Ê≥ï‰ºö
      this.fahuiType = 'special';
    }
    
    console.log('üîç ËÆæÁΩÆÊ≥ï‰ºöÁ±ªÂûã‰∏∫:', this.fahuiType)
    
    this.getConfig()
    this.getList()
  },
  methods: {
    async getConfig() {
      try {
        console.log('üîç Ëé∑ÂèñÊî∂‰ª∂‰ø°ÊÅØÈÖçÁΩÆÔºåÊ≥ï‰ºöÁ±ªÂûã:', this.fahuiType)
        const result = await fahuiManagement.getReceiverConfig({ type: this.fahuiType })
        console.log('üîç Ëé∑ÂèñÈÖçÁΩÆÁªìÊûú:', result)
        
        if (result.success) {
          this.enabled = !!result.data?.enabled
          console.log('üîç ËÆæÁΩÆÊî∂‰ª∂‰ø°ÊÅØÁä∂ÊÄÅ‰∏∫:', this.enabled)
        } else {
          console.error('üîç Ëé∑ÂèñÈÖçÁΩÆÂ§±Ë¥•:', result.message)
          this.enabled = false
        }
      } catch (e) {
        console.error('üîç Ëé∑ÂèñÊî∂‰ª∂‰ø°ÊÅØÈÖçÁΩÆÂºÇÂ∏∏:', e)
        this.enabled = false
      }
    },
    async onSwitchChange(e) {
      const value = e.detail.value
      console.log('üîç Êî∂‰ª∂‰ø°ÊÅØÂºÄÂÖ≥ÂèòÂåñÔºåÊñ∞ÂÄº:', value, 'Ê≥ï‰ºöÁ±ªÂûã:', this.fahuiType)
      
      try {
        const result = await fahuiManagement.updateReceiverConfig({ 
          type: this.fahuiType,
          enabled: value 
        })
        console.log('üîç Êõ¥Êñ∞ÈÖçÁΩÆÁªìÊûú:', result)
        
        if (result.success) {
          this.enabled = value
          uni.showToast({ title: value ? 'Â∑≤ÂêØÁî®' : 'Â∑≤ÂÖ≥Èó≠', icon: 'success' })
          console.log('üîç Êî∂‰ª∂‰ø°ÊÅØÁä∂ÊÄÅÊõ¥Êñ∞ÊàêÂäü:', this.enabled)
        } else {
          console.error('üîç Êõ¥Êñ∞ÈÖçÁΩÆÂ§±Ë¥•:', result.message)
          uni.showToast({ title: result.message || 'ËÆæÁΩÆÂ§±Ë¥•', icon: 'none' })
        }
      } catch (e) {
        console.error('üîç Êî∂‰ª∂‰ø°ÊÅØÂºÄÂÖ≥ËÆæÁΩÆÂºÇÂ∏∏:', e)
        uni.showToast({ title: 'ËÆæÁΩÆÂ§±Ë¥•', icon: 'none' })
      }
    },
    async getList() {
      this.loading = true
      try {
        console.log('üîç Ëé∑ÂèñÊî∂‰ª∂‰∫∫ÂàóË°®ÔºåÊ≥ï‰ºöÁ±ªÂûã:', this.fahuiType)
        const result = await fahuiManagement.getReceivers({ type: this.fahuiType })
        console.log('üîç Ëé∑ÂèñÊî∂‰ª∂‰∫∫ÂàóË°®ÁªìÊûú:', result)
        
        if (result.success) {
          this.receivers = result.data || []
          console.log('üîç Êî∂‰ª∂‰∫∫ÂàóË°®:', this.receivers)
        } else {
          console.error('üîç Ëé∑ÂèñÊî∂‰ª∂‰∫∫ÂàóË°®Â§±Ë¥•:', result.message)
          this.receivers = []
          uni.showToast({ title: result.message || 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
        }
      } catch (e) {
        console.error('üîç Ëé∑ÂèñÊî∂‰ª∂‰∫∫ÂàóË°®ÂºÇÂ∏∏:', e)
        this.receivers = []
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      }
      this.loading = false
    },
    async del(id) {
      console.log('üîç Âà†Èô§Êî∂‰ª∂‰∫∫ÔºåID:', id, 'Ê≥ï‰ºöÁ±ªÂûã:', this.fahuiType)
      
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËØ•Êî∂‰ª∂‰∫∫‰ø°ÊÅØÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              const result = await fahuiManagement.deleteReceiver({ 
                _id: id,
                type: this.fahuiType
              })
              console.log('üîç Âà†Èô§Êî∂‰ª∂‰∫∫ÁªìÊûú:', result)
              
              if (result.success) {
                uni.showToast({ title: 'Âà†Èô§ÊàêÂäü', icon: 'success' })
                this.getList()
              } else {
                console.error('üîç Âà†Èô§Êî∂‰ª∂‰∫∫Â§±Ë¥•:', result.message)
                uni.showToast({ title: result.message || 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
              }
            } catch (e) {
              console.error('üîç Âà†Èô§Êî∂‰ª∂‰∫∫ÂºÇÂ∏∏:', e)
              uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
            }
          }
        },
      })
    },
  },
}
</script>

